<template><div class="markdown-body"><h1>菜单</h1>
<p><code v-pre="">mu-menu</code> 组件展示一个菜单在用于激活它的元素的位置上。</p>
<h2 id="shi-li"><a class="header-anchor" href="#shi-li" aria-hidden="true">¶</a> 示例</h2>
<p>菜单的内容需要放置在 <code v-pre="">content</code> 插槽中</p>
<demo-block :jsfiddle="{&quot;html&quot;:&quot;&lt;mu-menu cover&gt;\n  &lt;mu-button color=\&quot;primary\&quot;&gt;dropdown&lt;/mu-button&gt;\n  &lt;mu-list slot=\&quot;content\&quot;&gt;\n    &lt;mu-list-item button&gt;\n      &lt;mu-list-item-title&gt;Refresh&lt;/mu-list-item-title&gt;\n    &lt;/mu-list-item&gt;\n    &lt;mu-list-item button&gt;\n      &lt;mu-list-item-title&gt;Send feedback&lt;/mu-list-item-title&gt;\n    &lt;/mu-list-item&gt;\n    &lt;mu-list-item button&gt;\n      &lt;mu-list-item-title&gt;Settings&lt;/mu-list-item-title&gt;\n    &lt;/mu-list-item&gt;\n    &lt;mu-list-item button&gt;\n      &lt;mu-list-item-title&gt;Help&lt;/mu-list-item-title&gt;\n    &lt;/mu-list-item&gt;\n    &lt;mu-list-item button&gt;\n      &lt;mu-list-item-title&gt;Sign out&lt;/mu-list-item-title&gt;\n    &lt;/mu-list-item&gt;\n  &lt;/mu-list&gt;\n&lt;/mu-menu&gt;\n&quot;,&quot;script&quot;:null,&quot;style&quot;:null}">
                    <div class="source" slot="source"><mu-menu cover="">
  <mu-button color="primary">dropdown</mu-button>
  <mu-list slot="content">
    <mu-list-item button="">
      <mu-list-item-title>Refresh</mu-list-item-title>
    </mu-list-item>
    <mu-list-item button="">
      <mu-list-item-title>Send feedback</mu-list-item-title>
    </mu-list-item>
    <mu-list-item button="">
      <mu-list-item-title>Settings</mu-list-item-title>
    </mu-list-item>
    <mu-list-item button="">
      <mu-list-item-title>Help</mu-list-item-title>
    </mu-list-item>
    <mu-list-item button="">
      <mu-list-item-title>Sign out</mu-list-item-title>
    </mu-list-item>
  </mu-list>
</mu-menu>
</div>
                    
                    <div class="highlight" slot="highlight"><pre v-pre="" class="hljs"><code v-pre=""><span class="hljs-tag">&lt;<span class="hljs-name">mu-menu</span> <span class="hljs-attr">cover</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">mu-button</span> <span class="hljs-attr">color</span>=<span class="hljs-string">"primary"</span>&gt;</span>dropdown<span class="hljs-tag">&lt;/<span class="hljs-name">mu-button</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">mu-list</span> <span class="hljs-attr">slot</span>=<span class="hljs-string">"content"</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">mu-list-item</span> <span class="hljs-attr">button</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">mu-list-item-title</span>&gt;</span>Refresh<span class="hljs-tag">&lt;/<span class="hljs-name">mu-list-item-title</span>&gt;</span>
    <span class="hljs-tag">&lt;/<span class="hljs-name">mu-list-item</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">mu-list-item</span> <span class="hljs-attr">button</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">mu-list-item-title</span>&gt;</span>Send feedback<span class="hljs-tag">&lt;/<span class="hljs-name">mu-list-item-title</span>&gt;</span>
    <span class="hljs-tag">&lt;/<span class="hljs-name">mu-list-item</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">mu-list-item</span> <span class="hljs-attr">button</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">mu-list-item-title</span>&gt;</span>Settings<span class="hljs-tag">&lt;/<span class="hljs-name">mu-list-item-title</span>&gt;</span>
    <span class="hljs-tag">&lt;/<span class="hljs-name">mu-list-item</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">mu-list-item</span> <span class="hljs-attr">button</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">mu-list-item-title</span>&gt;</span>Help<span class="hljs-tag">&lt;/<span class="hljs-name">mu-list-item-title</span>&gt;</span>
    <span class="hljs-tag">&lt;/<span class="hljs-name">mu-list-item</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">mu-list-item</span> <span class="hljs-attr">button</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">mu-list-item-title</span>&gt;</span>Sign out<span class="hljs-tag">&lt;/<span class="hljs-name">mu-list-item-title</span>&gt;</span>
    <span class="hljs-tag">&lt;/<span class="hljs-name">mu-list-item</span>&gt;</span>
  <span class="hljs-tag">&lt;/<span class="hljs-name">mu-list</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">mu-menu</span>&gt;</span>
</code></pre>
</div></demo-block>
<h2 id="xuan-ting-xian-shi"><a class="header-anchor" href="#xuan-ting-xian-shi" aria-hidden="true">¶</a> 悬停显示</h2>
<p><code v-pre="">open-on-hover</code> 将触发菜单显示/隐藏的事件变为 <code v-pre="">mouseenter</code> 和 <code v-pre="">mouseleave</code></p>
<demo-block :jsfiddle="{&quot;html&quot;:&quot;&lt;mu-menu placement=\&quot;top-start\&quot; open-on-hover&gt;\n    &lt;mu-button color=\&quot;primary\&quot;&gt;Open On Hover&lt;/mu-button&gt;\n    &lt;mu-list slot=\&quot;content\&quot;&gt;\n      &lt;mu-list-item button&gt;\n        &lt;mu-list-item-title&gt;Refresh&lt;/mu-list-item-title&gt;\n      &lt;/mu-list-item&gt;\n      &lt;mu-list-item button&gt;\n        &lt;mu-list-item-title&gt;Send feedback&lt;/mu-list-item-title&gt;\n      &lt;/mu-list-item&gt;\n      &lt;mu-list-item button&gt;\n        &lt;mu-list-item-title&gt;Settings&lt;/mu-list-item-title&gt;\n      &lt;/mu-list-item&gt;\n      &lt;mu-list-item button&gt;\n        &lt;mu-list-item-title&gt;Help&lt;/mu-list-item-title&gt;\n      &lt;/mu-list-item&gt;\n      &lt;mu-list-item button&gt;\n        &lt;mu-list-item-title&gt;Sign out&lt;/mu-list-item-title&gt;\n      &lt;/mu-list-item&gt;\n    &lt;/mu-list&gt;\n&lt;/mu-menu&gt;\n&quot;,&quot;script&quot;:null,&quot;style&quot;:null}">
                    <div class="source" slot="source"><mu-menu placement="top-start" open-on-hover="">
    <mu-button color="primary">Open On Hover</mu-button>
    <mu-list slot="content">
      <mu-list-item button="">
        <mu-list-item-title>Refresh</mu-list-item-title>
      </mu-list-item>
      <mu-list-item button="">
        <mu-list-item-title>Send feedback</mu-list-item-title>
      </mu-list-item>
      <mu-list-item button="">
        <mu-list-item-title>Settings</mu-list-item-title>
      </mu-list-item>
      <mu-list-item button="">
        <mu-list-item-title>Help</mu-list-item-title>
      </mu-list-item>
      <mu-list-item button="">
        <mu-list-item-title>Sign out</mu-list-item-title>
      </mu-list-item>
    </mu-list>
</mu-menu>
</div>
                    
                    <div class="highlight" slot="highlight"><pre v-pre="" class="hljs"><code v-pre=""><span class="hljs-tag">&lt;<span class="hljs-name">mu-menu</span> <span class="hljs-attr">placement</span>=<span class="hljs-string">"top-start"</span> <span class="hljs-attr">open-on-hover</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">mu-button</span> <span class="hljs-attr">color</span>=<span class="hljs-string">"primary"</span>&gt;</span>Open On Hover<span class="hljs-tag">&lt;/<span class="hljs-name">mu-button</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">mu-list</span> <span class="hljs-attr">slot</span>=<span class="hljs-string">"content"</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">mu-list-item</span> <span class="hljs-attr">button</span>&gt;</span>
        <span class="hljs-tag">&lt;<span class="hljs-name">mu-list-item-title</span>&gt;</span>Refresh<span class="hljs-tag">&lt;/<span class="hljs-name">mu-list-item-title</span>&gt;</span>
      <span class="hljs-tag">&lt;/<span class="hljs-name">mu-list-item</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">mu-list-item</span> <span class="hljs-attr">button</span>&gt;</span>
        <span class="hljs-tag">&lt;<span class="hljs-name">mu-list-item-title</span>&gt;</span>Send feedback<span class="hljs-tag">&lt;/<span class="hljs-name">mu-list-item-title</span>&gt;</span>
      <span class="hljs-tag">&lt;/<span class="hljs-name">mu-list-item</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">mu-list-item</span> <span class="hljs-attr">button</span>&gt;</span>
        <span class="hljs-tag">&lt;<span class="hljs-name">mu-list-item-title</span>&gt;</span>Settings<span class="hljs-tag">&lt;/<span class="hljs-name">mu-list-item-title</span>&gt;</span>
      <span class="hljs-tag">&lt;/<span class="hljs-name">mu-list-item</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">mu-list-item</span> <span class="hljs-attr">button</span>&gt;</span>
        <span class="hljs-tag">&lt;<span class="hljs-name">mu-list-item-title</span>&gt;</span>Help<span class="hljs-tag">&lt;/<span class="hljs-name">mu-list-item-title</span>&gt;</span>
      <span class="hljs-tag">&lt;/<span class="hljs-name">mu-list-item</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">mu-list-item</span> <span class="hljs-attr">button</span>&gt;</span>
        <span class="hljs-tag">&lt;<span class="hljs-name">mu-list-item-title</span>&gt;</span>Sign out<span class="hljs-tag">&lt;/<span class="hljs-name">mu-list-item-title</span>&gt;</span>
      <span class="hljs-tag">&lt;/<span class="hljs-name">mu-list-item</span>&gt;</span>
    <span class="hljs-tag">&lt;/<span class="hljs-name">mu-list</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">mu-menu</span>&gt;</span>
</code></pre>
</div></demo-block>
<h2 id="kong-zhi-xian-shi"><a class="header-anchor" href="#kong-zhi-xian-shi" aria-hidden="true">¶</a> 控制显示</h2>
<p><code v-pre="">open</code> 参数控制菜单的显示，可使用 <code v-pre="">.sync</code> 进行双向绑定。</p>
<demo-block :jsfiddle="{&quot;html&quot;:&quot;&lt;mu-menu cover placement=\&quot;bottom-end\&quot; :open.sync=\&quot;open\&quot;&gt;\n    &lt;mu-button icon&gt;\n      &lt;mu-icon value=\&quot;more_vert\&quot;&gt;&lt;/mu-icon&gt;\n    &lt;/mu-button&gt;\n    &lt;mu-list slot=\&quot;content\&quot;&gt;\n      &lt;mu-list-item button&gt;\n        &lt;mu-list-item-title&gt;Refresh&lt;/mu-list-item-title&gt;\n      &lt;/mu-list-item&gt;\n      &lt;mu-list-item button&gt;\n        &lt;mu-list-item-title&gt;Send feedback&lt;/mu-list-item-title&gt;\n      &lt;/mu-list-item&gt;\n      &lt;mu-list-item button&gt;\n        &lt;mu-list-item-title&gt;Settings&lt;/mu-list-item-title&gt;\n      &lt;/mu-list-item&gt;\n      &lt;mu-list-item button&gt;\n        &lt;mu-list-item-title&gt;Help&lt;/mu-list-item-title&gt;\n      &lt;/mu-list-item&gt;\n      &lt;mu-list-item button&gt;\n        &lt;mu-list-item-title&gt;Sign out&lt;/mu-list-item-title&gt;\n      &lt;/mu-list-item&gt;\n    &lt;/mu-list&gt;\n&lt;/mu-menu&gt;\n&lt;mu-button style=\&quot;margin-left: 8px;\&quot; @click=\&quot;open = !open\&quot;&gt;Toggle&lt;/mu-button&gt;\n\n&quot;,&quot;script&quot;:&quot;\nexport default {\n  data () {\n    return {\n      open: false\n    }\n  }\n};\n&quot;,&quot;style&quot;:null}">
                    <div class="source" slot="source"><mu-menu cover="" placement="bottom-end" :open.sync="open">
    <mu-button icon="">
      <mu-icon value="more_vert"></mu-icon>
    </mu-button>
    <mu-list slot="content">
      <mu-list-item button="">
        <mu-list-item-title>Refresh</mu-list-item-title>
      </mu-list-item>
      <mu-list-item button="">
        <mu-list-item-title>Send feedback</mu-list-item-title>
      </mu-list-item>
      <mu-list-item button="">
        <mu-list-item-title>Settings</mu-list-item-title>
      </mu-list-item>
      <mu-list-item button="">
        <mu-list-item-title>Help</mu-list-item-title>
      </mu-list-item>
      <mu-list-item button="">
        <mu-list-item-title>Sign out</mu-list-item-title>
      </mu-list-item>
    </mu-list>
</mu-menu>
<mu-button style="margin-left: 8px;" @click="open = !open">Toggle</mu-button>

</div>
                    
                    <div class="highlight" slot="highlight"><pre v-pre="" class="hljs"><code v-pre=""><span class="hljs-tag">&lt;<span class="hljs-name">mu-menu</span> <span class="hljs-attr">cover</span> <span class="hljs-attr">placement</span>=<span class="hljs-string">"bottom-end"</span> <span class="hljs-attr">:open.sync</span>=<span class="hljs-string">"open"</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">mu-button</span> <span class="hljs-attr">icon</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">mu-icon</span> <span class="hljs-attr">value</span>=<span class="hljs-string">"more_vert"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">mu-icon</span>&gt;</span>
    <span class="hljs-tag">&lt;/<span class="hljs-name">mu-button</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">mu-list</span> <span class="hljs-attr">slot</span>=<span class="hljs-string">"content"</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">mu-list-item</span> <span class="hljs-attr">button</span>&gt;</span>
        <span class="hljs-tag">&lt;<span class="hljs-name">mu-list-item-title</span>&gt;</span>Refresh<span class="hljs-tag">&lt;/<span class="hljs-name">mu-list-item-title</span>&gt;</span>
      <span class="hljs-tag">&lt;/<span class="hljs-name">mu-list-item</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">mu-list-item</span> <span class="hljs-attr">button</span>&gt;</span>
        <span class="hljs-tag">&lt;<span class="hljs-name">mu-list-item-title</span>&gt;</span>Send feedback<span class="hljs-tag">&lt;/<span class="hljs-name">mu-list-item-title</span>&gt;</span>
      <span class="hljs-tag">&lt;/<span class="hljs-name">mu-list-item</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">mu-list-item</span> <span class="hljs-attr">button</span>&gt;</span>
        <span class="hljs-tag">&lt;<span class="hljs-name">mu-list-item-title</span>&gt;</span>Settings<span class="hljs-tag">&lt;/<span class="hljs-name">mu-list-item-title</span>&gt;</span>
      <span class="hljs-tag">&lt;/<span class="hljs-name">mu-list-item</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">mu-list-item</span> <span class="hljs-attr">button</span>&gt;</span>
        <span class="hljs-tag">&lt;<span class="hljs-name">mu-list-item-title</span>&gt;</span>Help<span class="hljs-tag">&lt;/<span class="hljs-name">mu-list-item-title</span>&gt;</span>
      <span class="hljs-tag">&lt;/<span class="hljs-name">mu-list-item</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">mu-list-item</span> <span class="hljs-attr">button</span>&gt;</span>
        <span class="hljs-tag">&lt;<span class="hljs-name">mu-list-item-title</span>&gt;</span>Sign out<span class="hljs-tag">&lt;/<span class="hljs-name">mu-list-item-title</span>&gt;</span>
      <span class="hljs-tag">&lt;/<span class="hljs-name">mu-list-item</span>&gt;</span>
    <span class="hljs-tag">&lt;/<span class="hljs-name">mu-list</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">mu-menu</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">mu-button</span> <span class="hljs-attr">style</span>=<span class="hljs-string">"margin-left: 8px;"</span> <span class="hljs-attr">@click</span>=<span class="hljs-string">"open = !open"</span>&gt;</span>Toggle<span class="hljs-tag">&lt;/<span class="hljs-name">mu-button</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
<span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
  data () {
    <span class="hljs-keyword">return</span> {
      <span class="hljs-attr">open</span>: <span class="hljs-literal">false</span>
    }
  }
};
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
</code></pre>
</div></demo-block>
<h2 id="menu-props"><a class="header-anchor" href="#menu-props" aria-hidden="true">¶</a> Menu Props</h2>
<table>
<thead>
<tr>
<th>参数</th>
<th>介绍</th>
<th>类型</th>
<th>可选值</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>cover</td>
<td>弹出菜单是否覆盖触发元素</td>
<td>Boolean</td>
<td>—</td>
<td>false</td>
</tr>
<tr>
<td>placement</td>
<td>弹出位置</td>
<td>String</td>
<td>top / top-start / top-end / bottom / bottom-start / bottom-end / left / left-start / left-end / right / right-start / right-end</td>
<td>bottom-start</td>
</tr>
<tr>
<td>open</td>
<td>是否打开菜单</td>
<td>Boolean</td>
<td>—</td>
<td>false</td>
</tr>
<tr>
<td>open-on-hover</td>
<td>是否在悬停时打开菜单</td>
<td>Boolean</td>
<td>—</td>
<td>false</td>
</tr>
<tr>
<td>popover-class</td>
<td>弹出菜单的样式</td>
<td>String/Object/Array</td>
<td>—</td>
<td>—</td>
</tr>
</tbody>
</table>
<h2 id="menu-events"><a class="header-anchor" href="#menu-events" aria-hidden="true">¶</a> Menu Events</h2>
<table>
<thead>
<tr>
<th>名称</th>
<th>介绍</th>
<th>回调参数</th>
</tr>
</thead>
<tbody>
<tr>
<td>open</td>
<td>菜单打开时触发事件</td>
<td>—</td>
</tr>
<tr>
<td>close</td>
<td>菜单关闭时触发事件</td>
<td>—</td>
</tr>
</tbody>
</table>
<h2 id="menu-slots"><a class="header-anchor" href="#menu-slots" aria-hidden="true">¶</a> Menu Slots</h2>
<table>
<thead>
<tr>
<th>名称</th>
<th>介绍</th>
</tr>
</thead>
<tbody>
<tr>
<td>content</td>
<td>菜单内容插槽</td>
</tr>
<tr>
<td>default</td>
<td>菜单触发器插槽，只接受一个</td>
</tr>
</tbody>
</table>

</div class="markdown-body"></template>

<script>
export default {
  data () {
    return {
      open: false
    }
  }
};
</script>


